<template>
	<div class="city_box">
		<header>
			<Headnav head_left="ele.em" :head_right="start">
				<router-link v-if="listShow.start" to="/user" tag="span" slot="uplog">
					<van-icon name="contact" />
				</router-link>
				<router-link v-else to="/uplog" tag="span" slot="uplog">
					登录/注册
				</router-link>
			</Headnav>
		</header>
		<div class="city_cont">
			<div class="city_position">
				<p>当前定位城市:</p>
				<p>定位不准时,请在城市列表中选择</p>
			</div>
			<div class="city_route">
				<p>{{this.site.name}}</p>
				<router-link :to="{name:'login',query:{login:site.id}}" tag="p">
					<span class="el-icon-arrow-right"></span></router-link>
			</div>
			<p class="city_color"></p>
			<div class="city_hot">
				<p class="hot_txt">热门城市</p>
				<ul class="city_hot_list">
					<li>
						<router-link v-for="(item,index) in list" :key="index" :to="{name:'login',query:{login:item.id}}" tag="p">{{item.name}}</router-link>
					</li>
				</ul>
				<p class="city_color2"></p>

				<div class="city_click">
					<ul>
						<li v-for="(item,index) in Group" :key="index">
							<p class="hot_txt">{{index}}(按字母排序)</p>
							<div class="city_list1">
								<ul class="city_list">
									<router-link v-for="(i,k) in item" :key="k" :to="{path:'login',query:{login:i.id}}" tag="li">{{i.name}}</router-link>
								</ul>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Headnav from '../About/headNav'
	import {mapState,mapMutations} from 'vuex'
	export default {
		name: "city",
		components:{
			Headnav
		},
		data(){
			return{
				site:'',
				list:[],
				group:[],
				userId:'',
				start:false
			}
		},
		methods:{
			...mapMutations(['getlocalStorage'])
		},
		computed:{
			...mapState(['listShow']),
			Group(){
				let sotObj = {}
				for(let i=65;i<=90; i++){
					if(this.group[String.fromCharCode(i)]){
						sotObj[String.fromCharCode(i)]=this.group[String.fromCharCode(i)]
					}
				}
				return sotObj
			},
		},
		created(){
			this.getlocalStorage()
			this.$axios.get('http://elm.cangdu.org/v1/cities?type=guess').then(res=>{
				this.site=res.data;
			}),
				this.$axios.get('http://elm.cangdu.org/v1/cities?type=hot').then(res =>{
					this.list =res.data
				}),
			this.$axios.get('http://elm.cangdu.org/v1/cities?type=group').then(res =>{
				this.group=res.data
			})
		}
	}
</script>
